









/* eslint-disable no-undef */
import FormApi from '@/apis/FormApi';
import GoogleClient from '@/libs/GoogleClient';
import { moveFile } from '@/libs/MergeTextTemplate';
import Vue from 'vue';

export default Vue.extend({
  data() {
    return {
      results: [] as string[],
    };
  },
  methods: {
    async trigger() {
      const promises = [];

      for (let i = 0; i < 10; i += 1) {
        promises.push(FormApi.getStreamId());
      }

      const streamIds = await Promise.all(promises);

      this.results = streamIds.map((streamId) => String(streamId));
    },
  },
});
